import { scaleMobile } from "../../../..";

function bannerInlineStyleTcfStacksAccordion({
  bodyDesign: {
    tcfStacksBorderWidth,
    tcfStacksBorderColor,
    tcfStacksPadding,
    tcfStacksBg,
    tcfStacksHoverBg,
    tcfStacksActiveBg
  },
  customCss: {
    antiAdBlocker
  },
  mobile,
  pageRequestUuid4
}, isLast, isActive, isHover) {
  const border = "".concat(scaleMobile(mobile, pageRequestUuid4, tcfStacksBorderWidth), "px solid ").concat(tcfStacksBorderColor);
  const style = {
    padding: scaleMobile(mobile, pageRequestUuid4, tcfStacksPadding).map(i => "".concat(i, "px")).join(" "),
    borderTop: border,
    borderBottom: isLast ? border : undefined,
    cursor: "pointer",
    background: isHover ? tcfStacksHoverBg : isActive ? tcfStacksActiveBg : tcfStacksBg
  };
  return {
    className: antiAdBlocker === "y" ? undefined : "rcb-tcf-stacks-accordion",
    style
  };
}

export { bannerInlineStyleTcfStacksAccordion };