import { useMemo } from "react";
import { BannerTcfVendor } from ".";
import { useBanner, bannerGroupsInlineStyleGroup, BannerGroupSectionTitle } from "../../../..";

const BannerTcfVendorList = () => {
  const banner = useBanner();
  const {
    tcf: {
      gvl: {
        vendors: gvlVendors
      },
      model
    },
    tcfFilterBy,
    i18n: {
      tcf: {
        vendors,
        filterNoVendors
      }
    }
  } = banner;
  const useVendors = useMemo(() => Object.values(gvlVendors) // Only show vendor if suitable for the current filter
  .filter(({
    [tcfFilterBy === "consent" ? "purposes" : "legIntPurposes"]: usedPurposes
  }) => tcfFilterBy === "consent" ? true : usedPurposes.length > 0) // We need to manually sort it again cause the `vendor-list.json#vendors` property uses numeric keys and
  // JavaScript does automatically sort it
  .sort(({
    name: x
  }, {
    name: y
  }) => x < y ? -1 : x > y ? 1 : 0), [gvlVendors, tcfFilterBy, model]);
  const hasVendors = useVendors.length > 0;
  return h("div", bannerGroupsInlineStyleGroup(banner), h(BannerGroupSectionTitle, {
    title: vendors
  }, useVendors.map(({
    id
  }) => h(BannerTcfVendor, {
    key: id,
    id: id
  })), !hasVendors && filterNoVendors));
};

export { BannerTcfVendorList };