import { scaleMobile } from "../../../..";

function bannerInlineStyleTcfStacksDescription({
  bodyDesign: {
    tcfStacksDescriptionFontSize,
    tcfStacksDescriptionFontColor,
    tcfStacksDescriptionFontWeight,
    tcfStacksDescriptionMargin
  },
  customCss: {
    antiAdBlocker
  },
  mobile,
  pageRequestUuid4
}) {
  const style = {
    fontSize: scaleMobile(mobile, pageRequestUuid4, +tcfStacksDescriptionFontSize),
    color: tcfStacksDescriptionFontColor,
    fontWeight: tcfStacksDescriptionFontWeight,
    margin: scaleMobile(mobile, pageRequestUuid4, tcfStacksDescriptionMargin).map(i => "".concat(i, "px")).join(" ")
  };
  return {
    className: antiAdBlocker === "y" ? undefined : "rcb-tcf-stacks-title",
    style
  };
}

export { bannerInlineStyleTcfStacksDescription };