import { scaleMobile } from "../../../..";

function bannerInlineStyleTcfStacksAccordionArrow({
  bodyDesign: {
    tcfStacksTitleFontSize,
    tcfStacksArrowColor
  },
  customCss: {
    antiAdBlocker
  },
  mobile,
  pageRequestUuid4
}) {
  const useFontSize = scaleMobile(mobile, pageRequestUuid4, +tcfStacksTitleFontSize);
  const style = {
    width: useFontSize,
    height: useFontSize,
    flex: "0 0 ".concat(useFontSize, "px"),
    lineHeight: "".concat(useFontSize, "px"),
    float: "left",
    marginRight: 10,
    color: tcfStacksArrowColor
  };
  return {
    className: antiAdBlocker === "y" ? undefined : "rcb-tcf-stacks-arrow",
    style
  };
}

export { bannerInlineStyleTcfStacksAccordionArrow };