import { scaleMobile } from "../../../..";

function bannerInlineStyleTcfStacksContainer({
  bodyDesign: {
    tcfStacksMargin
  },
  customCss: {
    antiAdBlocker
  },
  mobile,
  pageRequestUuid4
}) {
  const style = {
    margin: scaleMobile(mobile, pageRequestUuid4, tcfStacksMargin).map(i => "".concat(i, "px")).join(" "),
    textAlign: "left",
    lineHeight: 1.5
  };
  return {
    className: antiAdBlocker === "y" ? undefined : "rcb-tcf-stacks-container",
    style
  };
}

export { bannerInlineStyleTcfStacksContainer };